<script>

    const DEBUG = false;
    import {backgroundColors} from './constants';
    import {hexToRGBA} from './utils';
    import {templateFilters} from '../../utils/template-filters';

    export default {
        name: 'SchedulerCalendarEmission',
        props: {
            emission: Object,
        },
        data() {
            return {
                isHover: false,
            }
        },
        methods: {
            dblclick: function () {
                this.$emit('dblclick', this.emission.obj.uuid);
                this.isHover = false;
            },
            onMouseOver: function () {
                this.$emit('mouseover', this.emission.obj.co.uuid);
                this.isHover = true;
            },
            onMouseLeave: function () {
                this.$emit('mouseleave');
                this.isHover = false;
            },

        },
        computed: {
            style() {
                const color = backgroundColors[this.emission.obj.color];
                return {
                    backgroundColor: (this.isHover) ? hexToRGBA(color, 1) : hexToRGBA(color, .50),
                    borderColor: hexToRGBA(color, 1),
                }
            },
        },
        filters: templateFilters,
    }
</script>
<style lang="scss" scoped>

        // background: red;
        .emission {
            // border: 1px solid rgba(0, 0, 0, 0.25);
            margin: 1px 0 1px 1px;
            background: rgba(255, 255, 255, 0.80);
            height: calc(100% - 1px);
            cursor: pointer;
            transition: background 100ms;
            position: relative;
            display: flex;
            flex-direction: column;
            border-top: 2px solid #fff;
            border-bottom: 2px solid #fff;

            &:hover {
                // background: rgba(126, 235, 157, 0.85);
                min-height: 20px;
                z-index: 90;
            }

            &__title {
                // background: rgba(0, 0, 0, .025);
                padding: 0 4px;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }

            &:hover & {
                &__title {
                    overflow: visible;
                    white-space: normal;
                    z-index: 91;
                }
            }

            &.is-highlighted {
                background: rgba(235, 193, 64, 0.85) !important;
                border-color: rgba(235, 193, 64, 0.85) !important;
            }

            &.is-dragged {
                opacity: .2;
            }

            // detail block, visible on hover
            &__details {
                background: #000;
                position: absolute;
                top: -2px;
                left: calc(100% + 4px);
                color: white;
                min-width: 140px;
                padding: 2px 4px 2px;
                z-index: 999;
                    text-align: center;

                &__title {
                    margin-top: 4px;
                }

                &__visual {
                    margin-top: 8px;
                    max-width: 130px;
                    text-align: center;
                    img {
                        max-width: 90px;
                        // object-fit: fill;
                    }
                }

                &__appendix {
                    margin: 4px 0 4px;
                }
            }
        }
</style>

<template>
    <div
        @dblclick="dblclick"
        @mouseover="onMouseOver"
        @mouseleave="onMouseLeave"
        :style="style"
        :class="{ 'is-highlighted': emission.highlighted, 'is-dragged': emission.dragged }"
        class="emission">
        <div
            class="emission__title">
            <span v-if="emission.obj.series">
                {{ emission.obj.series }}
                <!--
                <br>
                <small>{{ emission.obj.name }}</small>
                -->
            </span>
            <span v-else>
                {{ emission.obj.name }}
            </span>
        </div>
        <div v-if="isHover"
            class="emission__details">
            <div
                class="emission__details__title">
                <span v-if="emission.obj.series">
                    {{ emission.obj.series }}
                    <br>
                    <span>{{ emission.obj.name }}</span>
                </span>
                <span v-else>
                    {{ emission.obj.name }}
                </span>
            </div>
            <div
                v-if="emission.obj.image"
                class="emission__details__visual">
                <img :src="emission.obj.image">
            </div>
            <div
                class="emission__details__appendix">
                <span>
                    {{ emission.obj.timeStart|date('HH:mm') }}
                    -
                    {{ emission.obj.timeEnd|date('HH:mm') }}
                </span>
            </div>

        </div>

    </div>
</template>
