<script>
    export default {
        name: 'TagInputContainer',
        props: {
            id: {
                type: String,
                required: true,
            },
            required: {
                type: Boolean,
                required: false,
                default: false,
            },
            label: {
                type: String,
                required: false,
            },
            hideLabel: {
                type: String,
                required: false,
            },
            errors: {
                type: Array,
                required: false,
                default: function () {
                    return [];
                },
            },
            help: {
                type: String,
                required: false,
            },
            isCheckbox: {
                type: Boolean,
                required: false,
                default: false,
            },
        },
        data: function () {
            return {
                tags: [],
                newTag: ""
            }
        },
        mounted: function () {
            this.readTagsFromInput();
        },
        methods: {
            removeTag: function (index) {
                const tags = [...this.tags];
                tags.splice(index, 1);
                this.tags = tags;
            },
            readTagsFromInput: function () {
                // console.debug('this.$refs', this.$refs);
                const input = this.$refs.field.querySelector("input");
                this.tags = input.value.split(",");
            },
            writeTagsToInput: function () {
                const input = this.$refs.field.querySelector("input");
                // input.value = this.tags.join(",");
                input.value = this.tags.map(tag => `"${tag}"`).join(',');
            },
        },
        watch: {
            tags: function (tags) {
                this.writeTagsToInput();
            },
            newTag: function (newTag) {
                const lastChar = newTag.substr(-1);
                if(lastChar === ",") {
                    this.tags.push(newTag.slice(0, -1));
                    this.newTag = "";
                }
                // console.debug('newTag', newTag.substr(-1));
            },
        },
        computed: {
            hasErrors: function () {
                return (this.errors && this.errors.length);
            }
        },
    }
</script>
<template>
    <div
        :class="{'has-error': hasErrors, 'no-label': hideLabel}"
        class="input-container input-container--tag">
        <div v-if="(!hideLabel)" class="label" :for="id">
            {{ label }}
            <span v-if="required" class="label__required">*</span>
        </div>
        <div>
            <span v-for="(tag, index) in tags" :key="`tag-${index}`" class="tag">
                {{ tag }}
                <span class="tag__remove" @click="removeTag(index)">x</span>
            </span>
            <input v-model="newTag" placeholder="Add Tag">
        </div>

        <div>

        </div>

        <div class="field" ref="field">
            <slot name="default"></slot>
        </div>
        <div class="appendix">
            <div v-if="hasErrors" class="errors">
                <p v-for="(error, index) in errors" :key="(index + error.code)">
                    {{ error.message }}
                </p>
            </div>
            <p v-if="help" class="help">{{ help }}</p>
        </div>
    </div>
</template>
<style lang="scss" scoped>

    @import '../../../style/abstracts/variables';
    @import '../../../style/components/form';

    .input-container {
        @include input-container-grid;
    }

    .tag {
        display: inline-flex;

        background: black;
        margin: 0 4px 4px 0;
        padding: 2px 10px;
        text-transform: uppercase;
        color: white;

        &__remove {
            padding: 0 0 0 .5rem;
            color: white;
            cursor: pointer;
            opacity: .5;
            &:hover {
                opacity: 1;
            }
        }
    }

</style>
